    var init_country_select = (countriesSelect, parent) => {
        function formatCountry(country) {
            if (!country.id) {
                return country.text
            }
            var $country = $(
                '<span class="item-selection"></span>' +
                '<span class="flag-icon flag-icon-' + country.id.toLowerCase() + '"></span>' +
                '<span class="flag-text">' + country.text + '</span>'
            )
            return $country
        }

        //Assuming you have a select element with name country
        // e.g. <select name="name"></select>

        countriesSelect.select2({
            placeholder: 'Select a country',
            closeOnSelect: false,
            theme: 'bootstrap-5',
            width: 'off',
            // allowClear: true,
            templateResult: formatCountry,
            data: isoCountries,
            dropdownParent: parent
        }).on('select2:closing', function (e) {
            e.preventDefault()
        }).on('select2:closed', function (e) {
            countriesSelect.select2('open')
        }).on('select2:selecting', function (e) {
            //console.log(e);
        }).on('select2:open', function (e) {
            const evt = 'scroll.select2'
            $(e.target).parents().off(evt)
            $(window).off(evt)
            $('input.select2-search__field').prop('placeholder', 'Search for a country')
        })
        // countriesSelect.select2('open')
    };
