/* eslint-enable */

/* global jQuery */
'use strict';
(function ($) {
    $(document).ready(function () {
        header_init()
        navbar_collapse_init()
        tooltips_init()
        form_validations()
        $('.find-booster-form select').select2({
            theme: 'bootstrap-5',
            minimumResultsForSearch: -1,
            width: 'off',
            dropdownCssClass: 'find-booster-dropdown',
            dropdownParent: '.find-booster-form .form-control-wrapper'
        })
        quotes_slider_init()
        steps_title_height()
        hashLinkScroll(true)
        checkNavTabsScroll()
        checkBlogProgress()
        product_image_gallery_init()
        form_fields_init()

        $('.address-popup').on('show.bs.modal', function () {
            $('.address-popup .address-form select').select2({
                theme: 'bootstrap-5',
                minimumResultsForSearch: -1,
                width: 'off',
                dropdownCssClass: 'select-dropdown',
                dropdownParent: '.address-popup .address-form'
            })
        })

        if ($('.btn-increase-product')?.length) {
            $('.btn-increase-product').click(function (event) {
                event.preventDefault()
                event.stopPropagation()
                let parent = $(this).parent('.cart-product-count-wrapper')
                let count = parseInt(parent.find('.cart-product-count').text()) + 1
                parent.find('.cart-product-count').text(count)
                if (count > 1) {
                    parent.find('.btn-decrease-product').attr('disabled', false)
                }
            })
        }
        if ($('.btn-decrease-product')?.length) {
            $('.btn-decrease-product').click(function (event) {
                event.preventDefault()
                event.stopPropagation()
                let parent = $(this).parent('.cart-product-count-wrapper')
                let count = parseInt(parent.find('.cart-product-count').text() || 1) - 1
                if (count <= 1) {
                    $(this).attr('disabled', true)
                }
                count = count <= 1 ? 1 : count
                parent.find('.cart-product-count').text(count)
            })
        }
        if ($('.btn-remove-product')?.length) {
            $('.btn-remove-product').click(function (event) {
                event.preventDefault()
                event.stopPropagation()
                $(this).parents('.cart-product').remove()
                let count = parseInt($('#cart-count').text() || 1)
                if (count > 1) {
                    $('#cart-count').text(count - 1)
                    $('.navbar .btn-cart .count .number').text(count - 1)
                } else {
                    $('.navbar .btn-cart .count').remove()
                    $('#offcanvas-cart-label').text('My Cart')
                    $('#offcanvas-cart .offcanvas-body').html('<div class="cart-empty"><img src="./assets/images/empty-cart.svg" alt="Empty Cart" width="241" height="187"><span>Your cart is empty.</span></div>')
                    $('#offcanvas-cart .cart-footer').remove()
                }
            })
        }
        if ($('.pass-visibility')?.length) {
            $('.pass-visibility').click(function (event) {
                event.preventDefault()
                event.stopPropagation()
                $(this).toggleClass('show')
                let field = $(this).closest('form').find('.password-control')
                let value = field.attr('type')
                field.attr('type', value === 'text' ? 'password' : 'text')
            })
        }

        $('[href^="#"]').click(function (e) {
            if (decodeURIComponent(location.hash) === e.target.hash) {
                e.preventDefault()
                e.stopPropagation()
                hashLinkScroll()
            }
        })


    })
    $(window).scroll(function () {
        header_init()
        checkBlogProgress()
    })

    $(window).on('hashchange', function (e) {
        hashLinkScroll()
    })

    $(window).resize(function () {
        navbar_collapse_init()
        steps_title_height()
        checkNavTabsScroll()
    })

    function header_init() {
        if ($(document).scrollTop() > 1) {
            $('.site-header').addClass('affix-header')
        } else {
            $('.site-header').removeClass('affix-header')
        }
    }

    function hashLinkScroll(load) {
        if (location.hash && location.hash !== '#') {
            const anchorLocation = decodeURIComponent(location.hash)
            const anchorElement = document.querySelector(anchorLocation)
            if (anchorElement) {
                let headerHeight = !!load && !!window.pageYOffset ? 72 : document.querySelector('.site-header').clientHeight
                let offsetPosition = anchorElement.getBoundingClientRect().top + window.pageYOffset - headerHeight
                console.log(offsetPosition)
                if (anchorElement.offsetTop)
                    window.scrollTo({
                        top: offsetPosition,
                        behavior: 'smooth'
                    })
            }
        }
    }

    function checkNavTabsScroll() {
        if ($('.blogs-tabs .nav-tabs')?.length) {
            let navTabs = $('.blogs-tabs .nav-tabs')
            let navTabsObj = navTabs?.get(0)
            if (navTabsObj?.scrollWidth > navTabsObj?.clientWidth) {
                navTabs.addClass('scrollable')?.css('--min-width', navTabsObj.scrollWidth + 'px')
            } else {
                navTabs.removeClass('scrollable')
            }
        }
    }

    function checkBlogProgress() {
        if ($('.blog-page.has-progress')?.length) {
            let content = $('.blog-page.has-progress .blog-content-wrapper')
            // let progress = Math.floor((Math.max(($(document).scrollTop() - content?.offset().top), 0) / content?.height()) * 100)
            let progress = Math.floor((($(document).scrollTop() + $(window).height() - (2 * content?.offset().top)) / content?.height()) * 100)
            progress = progress <= 100 ? progress : 100
            content.find('.blog-progress')?.attr('aria-valuenow', progress)?.find('.progress-bar').width(progress + '%')
        }
    }

    function navbar_collapse_init() {
        if ($(window).innerWidth() < 992 || window.matchMedia('(max-width: 991px)').matches) {
            $('#main-navigation').on('show.bs.collapse', function () {
                $('.site-header').addClass('mobile-background')
            }).on('hide.bs.collapse', function () {
                $('.site-header').removeClass('mobile-background')
            })
        } else {
            $('.site-header').removeClass('mobile-background')
        }
    }


    function tooltips_init() {
        const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]')
        const tooltipList = [...tooltipTriggerList].map(tooltipTriggerEl => new bootstrap.Tooltip(tooltipTriggerEl))
    }

    function steps_title_height() {
        if ($(window).innerWidth() >= 576 || window.matchMedia('(min-width: 576px)').matches) {
            var maxHeight = 0
            $('.signals-steps h3')?.removeAttr('style').each(function () {
                var thisH = $(this).height()
                if (thisH > maxHeight) {
                    maxHeight = thisH
                }
            })
            $('.signals-steps h3')?.height(maxHeight)
        } else {
            $('.signals-steps h3')?.removeAttr('style')
        }
    }

    function product_image_gallery_init() {
        /*     $.fancybox.bind('[data-fancybox="gallery"]', {
                 mainClass: 'product-gallery-popup',
                 Thumbs: {
                     type: 'classic'
                 }
             })*/
        if ($('.product-images-slider')?.length) {
            $('.product-images-slider').slick({
                slidesToShow: 3,
                slidesToScroll: 1,
                infinite: false,
                arrows: true,
                dots: false,
                swipe: true
            })
        }
    }

    function quotes_slider_init() {
        if ($('.quotes-slider')?.length) {
            $('.quotes-slider').slick({
                centerMode: true,
                // infinite: false,
                centerPadding: '15%',
                slidesToShow: 3,
                // initialSlide: 2,
                responsive: [
                    {
                        breakpoint: 1700,
                        settings: {
                            centerPadding: '60px',
                            slidesToShow: 3
                            // initialSlide: 1
                        }
                    },
                    {
                        breakpoint: 1200,
                        settings: {
                            centerPadding: '30%',
                            slidesToShow: 1
                            // initialSlide: 1
                        }
                    },
                    {
                        breakpoint: 992,
                        settings: {
                            centerPadding: '25%',
                            slidesToShow: 1
                            // initialSlide: 1
                        }
                    },
                    {
                        breakpoint: 767,
                        settings: {
                            centerPadding: '10%',
                            slidesToShow: 1
                            // initialSlide: 1
                        }
                    },
                    {
                        breakpoint: 576,
                        settings: {
                            centerPadding: '32px',
                            slidesToShow: 1
                            // initialSlide: 0
                        }
                    }
                ]
            })
        }
    }

    function form_fields_init() {
        $('.address-form select').select2({
            theme: 'bootstrap-5',
            minimumResultsForSearch: -1,
            width: 'off',
            dropdownCssClass: 'select-dropdown',
            dropdownParent: '.address-form'
        })


        if ($('input[type="tel"]').length) {
            let tels = $('input[type="tel"]')

            tels.each(function () {
                $(this).on('change keyup blur', function () {
                    if ($(this).closest('form').hasClass('was-validated') && $(this).is(':invalid')) {
                        $(this).parent('.iti').addClass('is-invalid')
                    } else {
                        $(this).parent('.iti').removeClass('is-invalid')
                    }
                })
                window.intlTelInput(this, {
                    autoHideDialCode: false,
                    separateDialCode: true,
                    initialCountry: 'auto',
                    geoIpLookup: callback => {
                        fetch("https://ipapi.co/json")
                            .then(res => res.json())
                            .then(data => callback(data.country_code))
                            .catch(() => callback("us"));
                    },
                    // utilsScript: 'https://cdn.jsdelivr.net/npm/intl-tel-input@17.0.3/build/js/utils.js'
                })
            })

        }
    }

    function form_validations() {
        if (window.location.pathname === '/login.html') {
            const urlParams = new URLSearchParams(window.location.search)
            if (urlParams.get('forgot') === '1') {
                let authToast = bootstrap.Toast.getOrCreateInstance('#forgot-toast')
                authToast.show()
            } else if (urlParams.get('reset') === '1') {
                let authToast = bootstrap.Toast.getOrCreateInstance('#reset-toast')
                authToast.show()
            }
        }
        const forms = document.querySelectorAll('.needs-validation')
        // Loop over them and prevent submission
        Array.from(forms).forEach(form => {
            form.addEventListener('submit', event => {
                form.classList.add('validating')
                event.preventDefault()
                event.stopPropagation()
                if (!!form.checkValidity()) {
                    setTimeout(() => {
                        form.classList.remove('was-validated')
                        form.classList.remove('validating')
                        form.classList.add('subscribed')
                        $(form).find('.form-control').blur()
                        if (window.location.pathname !== form.getAttribute('action')) {
                            window.location.href = form.getAttribute('action')
                        }
                    }, 1000)
                } else {
                    setTimeout(() => {
                        form.classList.remove('validating')
                        form.classList.remove('subscribed')
                        form.classList.add('was-validated')
                    }, 1000)
                }


            }, false)
        })
    }
})(jQuery)
