'use strict';
(function ($) {
    $(document).ready(function () {
        const findBoosterForm = {category: ''}
        let quizData = {category: '', type: '', country: '', carrier: [], signal: []}
        let slider = $('.booster-quiz-slider')
        let countriesSelect = slider?.find('.select-countries-wrapper select')
        let progressTooltip = $('.quiz-progress-value').length ? bootstrap.Tooltip.getOrCreateInstance('.quiz-progress-value') : null
        let errorToast = $('#quiz-form-error').length ? bootstrap.Toast.getOrCreateInstance('#quiz-form-error') : null
        // $('#booster-quiz').modal('show')
        home_find_booster_submit(findBoosterForm)

        $('#booster-quiz').on('show.bs.modal', function () {
            if (slider.hasClass('slick-initialized')) {
                slider.find('.slick-list').addClass('slick-list-height')
                restart_quiz()
            }
            select_category_form()
        }).on('shown.bs.modal', function () {
            if (!slider.hasClass('slick-initialized')) {
                quiz_slider_init(slider, countriesSelect, !!quizData.category)
                init_country_select(countriesSelect, '.booster-quiz-slider .select-countries-wrapper')
                update_progress()
            } else {
                setTimeout(() => {
                    slider?.slick('setPosition')
                    slider.find('.slick-list').removeClass('slick-list-height')
                }, 50)
            }
        }).on('hide.bs.modal', function () {
            progressTooltip.hide()
            // countriesSelect.select2('destroy')
            // slider?.slick('destroy')
        })

        $('#restart-quiz').click(function (e) {
            e?.preventDefault()
            e?.stopPropagation()
            restart_quiz()
        })
        $('.quiz-btn.previous').click(function (e) {
            e?.preventDefault()
            e?.stopPropagation()
            slider.slick('slickPrev')
            update_progress()
            // errorToast.show()
        })

        $('.quiz-btn.next').click(function (e) {
            e?.preventDefault()
            e?.stopPropagation()
            let fieldName = $(this).data('fieldName')
            let values = ['country'].includes(fieldName)
                ? [countriesSelect.val()]
                : $(this).parents('form').find('input[name="' + fieldName + '"]:checked')?.map((index, item) => $(item).val())?.get()
            if (!!values?.filter(item => !!item)?.length) {
                quizData[fieldName] = ['carrier', 'signal'].includes(fieldName) ? values : values[0]
                errorToast.hide()
                let slick = slider.slick('getSlick')
                if (slick.currentSlide + 2 === slick.slideCount) {
                    $('.quiz-loader').removeClass('d-none')
                    slider.addClass('waiting-data')
                    console.log('quiz data:', quizData)
                    setTimeout(() => {
                        $('.quiz-loader').addClass('d-none')
                        slider.removeClass('waiting-data')
                        slider.slick('slickNext')
                        update_progress()
                    }, 2000)
                } else {
                    slider.slick('slickNext')
                    update_progress()
                }
            } else {
                errorToast.show()
            }
        })


        let select_category_form = () => {
            if (!!findBoosterForm?.category) {
                quizData.category = findBoosterForm?.category || ''
                $('.check-item input[name="category"][value="' + findBoosterForm?.category + '"]').prop('checked', true)
                findBoosterForm.category = ''
                $('.q1-welcome').addClass('d-none')
                $('.q2-welcome').removeClass('d-none')
            }
        }

        let restart_quiz = () => {
            if (!!countriesSelect.val()) {
                init_country_select(countriesSelect, '.booster-quiz-slider .select-countries-wrapper')
            }
            $('.quiz-form').trigger('reset')
            $('.q2-welcome').addClass('d-none')
            $('.q1-welcome').removeClass('d-none')
            quizData = {category: '', type: '', country: '', carrier: [], signal: []}
            slider?.slick('slickGoTo', !!findBoosterForm?.category ? 1 : 0)
            update_progress()
        }
        let update_progress = () => {
            $('.quiz-modal-body').animate({scrollTop: 0}, 500)
            progressTooltip.hide()
            let slick = slider.slick('getSlick')
            // let progress = ((slick.currentSlide + 1) / (slick.slideCount - 1)) * 100
            let progress = Math.floor(((slick.currentSlide + 1) / slick.slideCount) * 100)
            progress = progress <= 100 ? progress : 100
            if (progress === 100) {
                $('#booster-quiz .btn-cart').removeClass('d-none')
            } else {
                $('#booster-quiz .btn-cart').addClass('d-none')
            }
            $('.quiz-progress')?.attr('aria-valuenow', progress)?.find('.progress-bar').width(progress + '%')
            progressTooltip.setContent({'.tooltip-inner': progress + '%'})
            setTimeout(() => {
                progressTooltip.show()
            }, 600)
        }
        init_select_all()
    })


    let init_select_all = () => {
        $('.check-all input').on('change', function () {
            let selected = $(this).data('fieldSelectAll')
            $(this).parents('form').find('input[name="' + selected + '"]').prop('checked', !!$(this).prop('checked'))
        })

        $('.check-item input').on('change', function () {
            let selected = $(this).attr('name')
            let notSelected = $(this).parents('.quiz-options').find('input[name="' + selected + '"]')?.filter((index, item) => !$(item).prop('checked'))
            $(this).parents('.quiz-options').find('.check-all input[data-field-select-all="' + selected + '"]').prop('checked', !notSelected?.length)
        })
    }

    let quiz_slider_init = (slider, countriesSelect, startFromSecond) => {
        slider.slick({
            slidesToShow: 1,
            slidesToScroll: 1,
            initialSlide: !!startFromSecond ? 1 : 0,
            adaptiveHeight: true,
            infinite: false,
            arrows: false,
            dots: false,
            swipe: false
        }).on('afterChange', function (event, slick, currentSlide) {
            if (currentSlide === 2) {
                // init_country_select(countriesSelect)
                countriesSelect.select2('open')
            }
        })
    }

    let home_find_booster_submit = (findBoosterForm) => {
        $('.find-booster-form')?.on('submit', function (event) {
            event.preventDefault()
            event.stopPropagation()
            if (this.checkValidity()) {
                let inputs = {}
                $(this).serializeArray().forEach(field => {
                    inputs[field.name] = field.value
                })
                findBoosterForm.category = inputs['where-select']
                $('#booster-quiz').modal('show')
            }
        })
    }
})(jQuery)
