'use strict';
(function ($) {
    let filtersForm = $('.category-filter-form')

    $(document).ready(function () {

        //Country Form actions
        let modal = $('#category-change-country')
        let countryForm = $('.category-change-country-form')
        let countriesSelect = countryForm.find('.select-countries-wrapper select')
        modal.on('show.bs.modal', function () {
            init_country_select(countriesSelect, '.category-change-country-form .select-countries-wrapper')
            countriesSelect.val(filtersForm.find('input[name="country"]').val())
        }).on('shown.bs.modal', function () {
            countriesSelect.select2('open')
        }).on('hidden.bs.modal', function () {
            countriesSelect.val('')
        })

        countryForm?.on('submit', function (e) {
            e.preventDefault()
            e.stopPropagation()
            // let urlParams = new URLSearchParams(window.location.search)
            // urlParams.set('country', countriesSelect.val())
            let url = new URL(window.location.href)
            url.searchParams.set('country', countriesSelect.val())
            modal.modal('hide')
            window.history.pushState({path: url.toString()}, '', url.toString())
            writeCurrentCountry(countriesSelect.val())
            // setTimeout(() => window.location.search = urlParams, 500)
        })


        if (!!countryForm?.length) {
            getDefaultCountry()
        }
        if (!!filtersForm?.length) {
            getDefaultFilterData()
        }
        checkFilterPosition()

        $('.btn-filter-view').click(function (e) {
            e?.preventDefault()
            e?.stopPropagation()
            let target = $(this).data('bsTarget')
            if ($(target).length) {
                if ($(this).hasClass('less')) {
                    $(target)?.removeAttr('style')
                    $(this).removeClass('less').find('span').text('View All')
                } else {
                    $(target).css('max-height', $(target).find('.filter-checklist-holder')?.height())
                    $(this).addClass('less').find('span').text('View Less')
                }
            }
        })
        $('.filters-clear-btn').click(function (e) {
            e?.preventDefault()
            e?.stopPropagation()
            let target = $(this).data('bsTarget')
            if ($(target).length) {
                $(target)?.trigger('reset')?.trigger('submit')
                // let url = new URL(window.location.protocol + '//' + window.location.host + window.location.pathname)
                // window.history.pushState({ path: url.toString() }, '', url.toString())
                if (!!filtersForm?.length) {
                    getDefaultFilterData()
                }
            }
        })

        filtersForm.on('submit', function (e) {
            e.preventDefault()
            e.stopPropagation()
            let formData = $(this).serializeArray()
            if (history.pushState) {
                let type = formData?.filter(item => item?.name === 'type')?.shift()?.value || null
                let url = new URL(window.location.protocol + '//' + window.location.host + (type || window.location.pathname))
                formData?.forEach((item) => {
                    item?.name !== 'type' &&
                    url.searchParams.append(item?.name, item?.value)
                })
                !!type && type !== window.location.pathname
                    ? window.location.href = url.toString()
                    : window.history.pushState({path: url.toString()}, '', url.toString())
            }
        })

        filtersForm.find('input[type="checkbox"]').on('change', function (e) {
            if ($(window).innerWidth() >= 992 || window.matchMedia('(min-width: 992px)').matches) {
                filtersForm.submit()
            }
        })
    })

    $(window).on('popstate', function () {
        if (!!filtersForm?.length) {
            getDefaultFilterData()
        }
    })

    $(window).resize(function () {
        checkFilterPosition()
    })

    let checkFilterPosition = () => {
        if ($(window).innerWidth() >= 992 || window.matchMedia('(min-width: 992px)').matches) {
            $('#category-filter-offcanvas')?.offcanvas('hide')
            if (!$('#category-filter-aside .category-filter-form')?.length) {
                $('.category-filter-form').detach().appendTo('#category-filter-aside')
            }
        } else {
            if (!$('#category-filter-offcanvas .offcanvas-body .category-filter-form')?.length) {
                $('.category-filter-form').detach().appendTo('#category-filter-offcanvas .offcanvas-body')
            }
        }
    }
    let writeCurrentCountry = (country) => {
        let countryName = isoCountries?.filter(countryObj => countryObj.id === country)?.shift()?.text
        filtersForm.find('input[name="country"]').val(country)
        $('span#category-country').text(countryName || '...')
    }
    let getUserLocationCountry = () => {
        $.get('https://ipinfo.io', function (response) {
            writeCurrentCountry(response.country)
        }, 'jsonp')
    }
    let getDefaultCountry = () => {
        let urlParams = new URLSearchParams(window.location.search)
        let country = urlParams.get('country')
        if (!!country) {
            writeCurrentCountry(country)
        } else {
            navigator.geolocation.getCurrentPosition(getUserLocationCountry, getUserLocationCountry)
        }
    }

    let getDefaultFilterData = () => {
        let urlParams = new URLSearchParams(window.location.search)
        let defaultValue = filtersForm.find('.price-range')?.data('sliderValue')
        let price = [urlParams.get('price-min') || defaultValue[0], urlParams.get('price-max') || defaultValue[1]]
        filtersForm.find('.price-range').slider('setValue', price)
        filtersForm.find('input[name="price-min"]')?.val(price[0])
        filtersForm.find('input[name="price-max"]')?.val(price[1])

        let providers = urlParams.getAll('provider')
        filtersForm.find('input[name="provider"]')?.filter((index, item) => providers.includes(item.value))?.prop('checked', true)

        let frequencies = urlParams.getAll('frequency')
        filtersForm.find('input[name="frequency"]')?.filter((index, item) => frequencies.includes(item.value))?.prop('checked', true)

        initPriceRange()
    }

    let initPriceRange = () => {
        let min = $('.price-range-fields input[name="price-min"]')
        let max = $('.price-range-fields input[name="price-max"]')
        let range = $('.price-range')
        let defaultValue = range?.data('sliderValue')
        range.slider().on('slide', function (ev) {
            min.val(ev.value[0])
            min.width(function () {
                return ((this.value.length || 1) * 8.5) + 16
            })
            max.val(ev.value[1])
            max.width(function () {
                return ((this.value.length || 1) * 8.5) + 16
            })
            defaultValue = ev.value
        })

        $('.price-field').width(function () {
            return ((this.value.length || 1) * 8.5) + 16
        }).on('change keydown input', function (e) {
            if (parseInt(this.value) >= parseInt($(this).attr('max'))) {
                this.value = $(this).attr('max')
            } else if (parseInt(this.value) <= parseInt($(this).attr('min'))) {
                this.value = $(this).attr('min')
            }
            $(this).width(function () {
                return ((this.value.length || 1) * 8.5) + 16
            })
        })

        min.on('change keydown input', function (e) {
            range.slider('setValue', [e.target.value, defaultValue[1]])
            defaultValue[0] = e.target.value
        })

        max.on('change keydown input', function (e) {
            range.slider('setValue', [defaultValue[0], e.target.value])
            defaultValue[1] = e.target.value
        })
    }

})(jQuery)
/* global jQuery */
'use strict';
(function ($) {
    $(document).ready(function () {
        let slider = $('.ask-question-slider')
        let progressWrapper = $('.ask-question-progress')
        let footerWrapper = $('.ask-question-footer')
        let modalLabel = $('#ask-question-label')
        let errorToast = $('#ask-question-form-error').length ? bootstrap.Toast.getOrCreateInstance('#ask-question-form-error') : null


        $('#ask-question-popup').on('show.bs.modal', function () {
            if (slider.hasClass('slick-initialized')) {
                slider?.slick('slickGoTo', 0)
                // slider?.slick('setPosition')
                $('.ask-question-form').trigger('reset')
                update_progress()
            }
            // update_progress()
        }).on('shown.bs.modal', function () {
            if (!slider.hasClass('slick-initialized')) {
                ask_question_slider_init(slider)
            } else {
                setTimeout(() => {
                    slider?.slick('setPosition')
                }, 50)
            }
            update_progress()
        }).on('hide.bs.modal', function () {
            // slider?.slick('destroy')
        })

        progressWrapper.find('.progress-btn').click(function (e) {
            e?.preventDefault()
            e?.stopPropagation()
            slider.slick('slickGoTo', parseInt(e.target.value || 1) - 1)
            update_progress()
            // errorToast.show()
        })

        $('.ask-question-next-btn').click(function (e) {
            e?.preventDefault()
            e?.stopPropagation()
            let slick = slider.slick('getSlick')
            let isValid = slider.find('.slick-current input, .slick-current textarea')?.filter((index, input) => !!input.value)?.length
            let validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
            let emailField = slider.find('.slick-current input[type="email"]')
            let isEmail = !!emailField?.length ? (!!emailField?.val() && emailField?.val()?.match(validRegex)) : false
            if (!isValid || (!!emailField?.length && !isEmail)) {
                errorToast.show()
            } else {
                errorToast.hide()
                if (slick.currentSlide + 2 === slick.slideCount) {
                    let askData = $('.ask-question-form').serialize()
                    console.log('ask data:', askData)
                }
                slider.slick('slickNext')
                update_progress()
            }
        })

        let update_progress = () => {
            let slick = slider?.slick('getSlick')
            let progress = slick?.currentSlide + 1
            switch (progress) {
                case 2:
                    progressWrapper.find('button[value="2"]').prop('disabled', false)
                    break
                case 3:
                    slider.addClass('fix-margin')
                    modalLabel.addClass('invisible opacity-0')
                    footerWrapper.addClass('d-none')
                    break
                default:
                    slider.removeClass('fix-margin')
                    modalLabel.removeClass('invisible opacity-0')
                    footerWrapper.removeClass('d-none')
                    progressWrapper.find('button[value="2"]').prop('disabled', true)
                    break
            }
        }
    })


    let ask_question_slider_init = (slider) => {
        slider.slick({
            slidesToShow: 1,
            slidesToScroll: 1,
            infinite: false,
            // adaptiveHeight: true,
            arrows: false,
            dots: false,
            swipe: false
        })
    }
})(jQuery)
